.listItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: start;
    margin: 8px 0;
    background: rgb(241, 241, 241);
    border-radius: 4px;
    padding: 0 10px;
    box-sizing: border-box;
}

.item-completed {
    background-color: #91ffb6;
}

.listItem span {
    display: grid;
    grid-template-columns: auto auto;
}

input[type="checkbox"] {
    position: relative;
    align-self: center;
    width: 24px;
    height: 24px;
    appearance: none;
    background-color: #fff;
    border: 2px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
}

input[type="checkbox"]:checked {
    background-color: #21f367; /* Color del fondo cuando est√° marcado */
    border-color: #21f367;
}

input[type="checkbox"]:checked::after {
    content: '\2714'; /* Unicode para el checkmark */
    font-size: 16px;
    color: white;
    position: absolute;
    top: 0;
    left: 4px;
}

svg:hover{
    cursor: pointer;
}

p {
    margin: 12px;
}

.p-completed {
    text-decoration: line-through;
}