form {
    display: flex;
    z-index: 1;
    flex-direction: column;
    background-color: white;
    padding: 24px;
    border-radius: 4px;
    justify-content: space-between;
    gap: 16px;
}

label {
    font-size: 20px;
    font-weight: 700;
}

textarea {
    border: 2px solid black;
    border-radius: 4px;
    box-shadow: 0px 5px 20px rgba(32, 35, 41, 0.25);
    font-size: 16px;
    resize: none;
    text-align: center;
}

textarea::placeholder{
    color: #B3B3B3;
}

textarea:focus {
    outline-color: rgb(133, 255, 255);;
}

.TodoForm-buttonContainer {
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 32px;
}

.TodoForm-button {
    border-radius: 2px;
    width: 80px;
    border: none;
    outline: none;
    cursor: pointer;
}

.TodoForm-button--cancel {
    background: transparent;
}

.TodoForm-button--add {
    background-color: rgb(133, 255, 255);
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}